<template>
  <!-- 사이즈 선택 -->
  <div class="d-flex border-top pad" style="flex-direction: column">
    <div class="col-auto d-flex justify-content-between" style="width: 100%">
      <label class="col-form-label">사이즈</label>
      <p class="card-title pt-3 pb-3">{{ selectedPrice }}</p>
    </div>
    <div class="d-flex justify-content-between col-12">
      <div class="col-2 d-grid p-1">
        <font-awesome-icon :icon="['fas', 'mug-saucer']" class="fa-sm" />
        <button
          @click="updatePrice(product[0].price.small), click1()"
          type="button"
          :class="{ 'btnn': click01, 'btn-clicked': !click01 }"
        >
          S
        </button>
      </div>
      <div class="col-2 d-grid p-1">
        <font-awesome-icon :icon="['fas', 'mug-saucer']" class="fa-lg" />
        <button
          @click="updatePrice(product[0].price.medium), click2()"
          type="button"
          :class="{ 'btnn': click02, 'btn-clicked': !click02 }"
        >
          <p>M</p>
        </button>
      </div>
      <div class="col-2 d-grid p-1">
        <font-awesome-icon :icon="['fas', 'mug-saucer']" class="fa-2x" />
        <button
          @click="updatePrice(product[0].price.large), click3()"
          type="button"
          :class="{ 'btnn': click03, 'btn-clicked': !click03 }"
        >
          <p>L</p>
        </button>
      </div>
    </div>

  </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

export default {

  data() {
    return {
      selectedPrice: 0, //클릭
      candy: 10,
      size: 0,
      click01: false,
      click02: false,
      click03: false
    };
  },
  props: {
    product: Object,
  },
  methods: {
    click1() {
        this.stateId = 0;
        this.click01 = true;
        this.click02 = false;
        this.click03 = false;
      },
    click2() {
        this.stateId = 0;
        this.click01 = false;
        this.click02 = true;
        this.click03 = false;
      },
    click3() {
      this.stateId = 0;
      this.click01 = false;
      this.click02 = false;
      this.click03 = true;
    },
    updatePrice(size) {
      console.log('클릭 가격' + size);
      this.selectedPrice = size;
      this.emitter.emit("selectedPrice", this.selectedPrice);
    }
  },
  computed: {
    SizePrice() {
      // 선택된 size에 따라 가격을 반환
      return this.selectedPrice;
    }
  },
};
</script>

<style scoped>

.btn-clicked { /* 클릭후 */
  border: solid 2px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#fff3f1;
  color:#1C2474;
}

.btnn { /* 클릭전 */
  border: solid 1px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#1C2474;
  color:#ffffff;
}

</style>