<template>
    <div>
        <!-- 온도 선택 -->
        <div class="d-flex justify-content-between align-items-center border-top m-10 pad">
        <div class="col-auto">
            <label class="col-form-label bold">온도 선택</label>
        </div>
        <!-- v-model -->
        <div class="col-5 d-grid p-1">
            <button type="button" :class="{ 'btnn':click01, 'btn-clicked': !click01 }" @click="updateTemp(1), click1()">Hot</button>
        </div>
        <div class="col-5 d-grid p-1">
            <button type="button" :class="{ 'btnn':click02, 'btn-clicked': !click02 }" @click="updateTemp(2), click2()">ICED</button>
        </div>
        </div>

        <!-- 컵 선택 -->
        <div class=" d-flex justify-content-between align-items-center border-top pad">
        <div class="col-auto">
            <label class="col-form-label bold">컵 선택</label>
        </div>
        <div class="col-5 d-grid p-1">
            <button type="button" :class="{ 'btnn':click03, 'btn-clicked': !click03 }" @click="updateCup(1), click3()">매장컵</button>
        </div>
        <div class="col-5 d-grid p-1">
            <button type="button" :class="{ 'btnn':click04, 'btn-clicked': !click04 }" @click="updateCup(2), click4()">일회용컵</button>
        </div>
        </div>

        <!-- 픽업 선택 -->
        <div class=" d-flex justify-content-between align-items-center border-top pad">
        <div class="col-auto">
            <label class="col-form-label bold">픽업을 어떻게 하시겠어요?</label>
        </div>
        <div class="col-2 d-grid">                                
            <button type="button" :class="{ 'btnn': click05, 'btn-clicked': !click05 }" @click="updatePickup(1), click5()">
                <font-awesome-icon :icon="['fas', 'mug-saucer']" />
                <p> 매장이용</p>
                <p> 매장에서 먹을게요 </p>
            </button>
        </div>
        <div class="col-2 d-grid p-1">                                
            <button type="button" :class="{ 'btnn': click06, 'btn-clicked': !click06 }" @click="updatePickup(2), click6()">
                <font-awesome-icon :icon="['fas', 'shoe-prints']" />
                <p> To-go </p>
                <p> 밖으로 가져갈게요</p>
            </button>
        </div>
        <div class="col-2 d-grid p-1">          
            <button type="button" :class="{ 'btnn': click07, 'btn-clicked': !click07 }" @click="updatePickup(3), click7()">
                <font-awesome-icon :icon="['fas', 'motorcycle']" />
                <p> 딜리버스 </p>
                <p> 배달로 받을게요 </p>
            </button>
        </div>
        </div>
    </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

export default {
    data() {
        return {
            click01: false,
            click02: false,
            click03: false,
            click04: false,
            click05: false,
            click06: false,
            click07: false,
            product: {
                temp: 0,
                cup: 0,
                pickup: 0,
            }
        }
    },
    computed: {
        user() {
            return this.$store.state.user;            
        }
    },
    methods: {
        click1() {
            this.stateId = 0;
            this.click01 = true;
            this.click02 = false;
        },
        click2() {
            this.stateId = 0;
            this.click01 = false;
            this.click02 = true;
        },
        click3() {
            this.stateId = 0;
            this.click03 = true;
            this.click04 = false;
        },
        click4() {
            this.stateId = 0;
            this.click03 = false;
            this.click04 = true;
        },
        click5() {
            this.stateId = 0;
            this.click05 = true;
            this.click06 = false;
            this.click07 = false;
        },
        click6() {
            this.stateId = 0;
            this.click05 = false;
            this.click06 = true;
            this.click07 = false;
        },
        click7() {
            this.stateId = 0;
            this.click05 = false;
            this.click06 = false;
            this.click07 = true;
        },   
        updateTemp(value) {
            this.product.temp = value;
            this.$emit('updateProduct', this.product);
        },
        updateCup(value) {
            this.product.cup = value;
            this.$emit('updateProduct', this.product);
        },
        updatePickup(value) {
            this.product.pickup = value;
            this.$emit('updateProduct', this.product);
        }
    }
}
</script>

<style scoped>
.bold {
    font-size: 16px;
    font-weight: bold;
}

.btn-clicked { /* 클릭후 */
  border: solid 2px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#fff3f1;
  color:#1C2474;
}

.btnn { /* 클릭전 */
  border: solid 1px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#1C2474;
  color:#ffffff;
}

</style>