          </div>
          <div class="modal-wrap" v-show="modalcom">
            <div class="modal-com">
              <strong class="text-nowrap" style="font-size: 35px; line-height: 20px; letter-spacing: 6px; font-weight: bold; margin-left: -50px">결제 완료</strong>
