<script>
// eslint-disable-next-line
/* eslint-disable */

export default {
  data() {
    return {
      productId: 0,
      productList: [],
      count: 5,
      timer: null,
      modalCheck: false,
      modalcom: false,
    };
  },
  mounted () {
      this.startTimer();
    },
  created() {
    this.productId = this.$route.query.product_id;
    this.getProductList();
  },
  computed: {
    buttonClass() {
    return this.isClicked ? 'btnn' : 'btn-clicked';
    }
  },
  methods: {
     startTimer() {
      this.timer = setInterval(() => {
  if(this.count > 0) {
  this.count--;
    } else {
    this.stopTimer();
    }
    }, 1000);
    },
    stopTimer() {
    clearInterval(this.timer);
    this.modalcom =false;
      this.modalCheck= false;
    },
    beforeDestroy() {
    this.stopTimer();
    },
    modalOpen() {
      this.modalCheck = !this.modalCheck;
      this.count= 5;
    },
    modalc() {
      this.modalcom = true;
      this.count = 5;
    },
    goToList() {
      this.$router.push({path:'/list'});
      this.isClicked = !this.isClicked; // 클릭 상태를 토글
    },
    async getProductList() {
      this.productList = await this.$api("/api/productList2",{ param: [this.productId] });
      console.log('this.productList2 는??',this.productList);
    },
    goToInsert() {
      this.$router.push({path:'/create'}); //페이지 router이동
    },
    goToDetail(product_id) {
     this.$router.push({path:'/detail', query:{product_id:product_id}}); 
    },
    goToUpdate(product_id) {
      this.$router.push({path:'/Update', query:{product_id:product_id}}); 
    },
    goToImageInsert(product_id) {
      this.$router.push({path:'/image_insert', query:{product_id:product_id}}); 
    },
    deleteProduct(product_id) {
      this.$swal.fire({
        title: '정말 삭제하시겠습니까?',
        showCancelButton: true,
        confirmButtonText: `삭제`,
        cancelButtonText: `취소`
      }).then(async (result) => {
        if (result.isConfirmed) {
          console.log(product_id)
          await this.$api("/api/productDelete",{param:[product_id]});
          this.getProductList();
          this.$swal.fire('삭제되었습니다!', '', 'success')
        } 
      });
    }
  }
}

</script>

