<template>
<!--  eslint-disable-next-line -->
<!--  eslint-disable -->
<div>
 <!DOCTYPE html>
 <html lang="en">
    <head>
        <meta charset="UTF-8">
        <meta name="viewport" content="width=device-width, initial-scale=1.0">
        <title>쇼핑몰페이지</title>
    </head>
    <body>
    <main class="mt-3">
        <div class="container">
            <!-- 탭 -->
          <div class="d-flex flex-row" style="width:100%; margin-bottom:15px;">
                <div :class="{ 'btnn': click01, 'btn-clicked': !click01 }" @click="click1()" style="width:34%; border-top-left-radius:20px;">
                <router-link to="/list" style="text-decoration-line: none; color:#green;">
                <div class="d-flex justify-content-center border_bt p-3">전체</div>
                </router-link>
                </div>
                <div :class="{ 'btnn': click02, 'btn-clicked': !click02 }" @click="click2()" style="width:33%">
                    <router-link to="/pet" style="text-decoration-line: none; color:#green;">
                <div class="d-flex justify-content-center border_bt p-3">반려견</div>
                </router-link>
                </div>
                <div :class="{ 'btnn': click03, 'btn-clicked': !click03 }" @click="click3()" style="width:33%; border-top-right-radius:20px;">
                <router-link to="/people" style="text-decoration-line: none; color:#green;">
                <div class="d-flex justify-content-center border_bt p-3">주인님들</div>
                </router-link>
                </div>
            </div>

            <div class="row mb-2">  
            <div class="col" style="width:60px">
                <select class="form-select" v-model="sortOption">
                <option value="1">전체 메뉴</option>
                <option value="2">낮은 가격순</option>
                <option value="3">높은 가격순</option>
                </select>
            </div>
            </div>
            <div class="row">
                <div class="col-xl-3 col-lg-4 col-md-6" :key="i" v-for="(product, i) in sortedProductList">
                    <div class="card" style="width: 18rem;">
                        <a @click="goToDetail(product.id);" style="cursor:pointer;">
                        <img :src="`/download/${product.id}/${product.path}`" class="card-img-top" alt="..."></a>
                        <div class="card-body">
                            <h5 class="card-title bold">{{ product.product_name }}</h5>
                            <p class="card-title">{{ product.product_subtitle }}</p>
                            <!-- <p class="card-title">{{ product.price }}</p> -->
                            <p class="card-text">
                                <span class="badge bg-dark me-2" style="background-color: #1C2474;">{{ product.tags }}</span>
                            </p>
                            <div class="d-flex justify-content-between align-items-center">
                                <div class="btn-group" role="group">
                                    <button type="button" class="btn btn-sm btn-outline-secondary">장바구니 담기</button>
                                    <button type="button" class="btn btn-sm btn-outline-secondary" @click="goToDetail(product.id);">주문하기</button>
                                </div>
                                <small class="text-dark fontmbold" >{{ getCurrencyFormat(product.price) }}원</small>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </main>                
    </body>
</html>
</div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */
 
export default {
    data() {
        return{
            click01: false,
            click02: false,
            click02: false,            
            sortOption: '1',
            productList: []
        };
    },
    created() {
        this.getProductList();
    },
    computed: {
  sortedProductList() {
    let sortedArray = [...this.productList]; // 원본 배열 복사
    if (this.sortOption === '2') { // 낮은 가격순
      return sortedArray.sort((a, b) => a.price - b.price);
    } else if (this.sortOption === '3') { // 높은 가격순
      return sortedArray.sort((a, b) => b.price - a.price);
    } else {
      return sortedArray; // 복사한 원본 배열 반환
    }
  }
},
    methods: {
        getCurrencyFormat(value){ //CurrencyFormat mixins
            return this.$currencyFormat(value);
        },
        click1() {
        this.stateId = 0;
        this.click01 = true;
        this.click02 = false;
        this.click03 = false;
        },
        click2() {
        this.stateId = 0;
        this.click01 = false;
        this.click02 = true;
        this.click03 = false;        
        },
        click3() {
        this.stateId = 0;
        this.click01 = false;
        this.click02 = false;
        this.click03 = true;        
        },
        async getProductList(){
            this.productList = await this.$api("/api/peopleList",{}); //await 어떤 신호가 전달될때까지 기다림, $ api 함수  
            console.log(this.productList);
        },
        goToDetail(product_id){
            this.$router.push({
                path:'/detail', query:{product_id: product_id} //자동으로 detail페이지로 이동
            });
        }
    }
}
</script>

<style scoped>
@import "../assets/css/file.css";

.border_bt {
    /* border-bottom: solid 1px red; */
    font-size: 18px;
    font-weight: bold;
}

/* .bold {
    font-size: 16px;
    font-weight: bold;
} */

.btn-clicked { /* 클릭후 */
  border-bottom: solid 1px #1C2474;
  padding: 6px;
  background-color:#fff3f1;
  color:#1C2474;
}

.btnn { /* 클릭전 */
  border-bottom: solid 1px #1C2474;
  padding: 6px;
  background-color:#1C2474;
  color:#ffffff;
}
</style>